import React from "react"
import "./App.css"
import {
    HashRouter,
    Routes,
    Route
} from "react-router-dom"
import { Home } from "./features/home"
import { Shop } from "./features/shop"

const App = () => {
    return (
        <HashRouter>
            <Routes>
                <Route path="/" element={<Home/>} />
                <Route path="/shop/*" element={<Shop/>}/>
                <Route path="*" element={<p>404</p>}/>
            </Routes>
        </HashRouter>
    )
}

export default App
